
.shogi-player .board_turn.turned {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.shogi-player {
  font-family: "YuMincho", "Yu Mincho", serif;
  margin: 0 auto;
  text-align: center;
}
.shogi-player .board_turn {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: all 0.2s 0s ease-in-out;
    transition: all 0.2s 0s ease-in-out;
}
.shogi-player .board_container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.shogi-player .board_container .flex_item:nth-child(1) {
      -ms-flex-item-align: start;
          align-self: flex-start;
}
.shogi-player .board_container .flex_item:nth-child(2) {
      -ms-flex-item-align: center;
          align-self: center;
}
.shogi-player .board_container .flex_item:nth-child(3) {
      -ms-flex-item-align: end;
          align-self: flex-end;
}
.shogi-player .board_container .board_wrap {
      position: relative;
}
.shogi-player .board_container .board_wrap .overlay_navi {
        position: absolute;
        z-index: 1;
        width: 75%;
        height: 100%;
}
.shogi-player .board_container .board_wrap .overlay_navi.previous {
          left: -25%;
}
.shogi-player .board_container .board_wrap .overlay_navi.next {
          right: -25%;
}
.shogi-player .board_container .board_wrap .overlay_navi.board_turn_area {
          z-index: 2;
          left: 44.44444%;
          width: 11.11111%;
          top: 44.44444%;
          height: 11.11111%;
}
.shogi-player .board_container .board_wrap .board {
        margin: 0 auto;
        padding: 0;
}
.shogi-player .board_container .board_wrap .board td {
          text-align: center;
}
.shogi-player .board_container .board_wrap .board td.location_black {
            vertical-align: bottom;
}
.shogi-player .board_container .board_wrap .board td.location_white {
            vertical-align: top;
}
.shogi-player .controller_block {
    margin-top: 1vmin;
    margin-bottom: 0vmin;
}
.shogi-player .controller_block button.next {
      width: 16vmin;
}
.shogi-player .slider {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    width: 37vmin;
}
.shogi-player .turn_edit_input {
    width: 3rem;
}
.shogi-player .turn_edit_text {
    cursor: pointer;
}
.shogi-player .piece_stand {
  min-width: 8vmin;
  margin: 0 1vmin;
  text-align: left;
}
.shogi-player .piece_stand.location_white {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.shogi-player .piece_stand.turn_active .location_mark {
    text-shadow: 0 0 0.5rem #bf4040;
}
.shogi-player .piece_stand ul {
    list-style-type: none;
    padding: 0 0.25vmin;
    margin: 1vmin;
}
.shogi-player .piece_stand ul li {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      color: #333333;
      font-size: 2.6vmin;
}
.shogi-player .piece_stand ul li .piece_count {
        vertical-align: middle;
        margin-left: 0.3rem;
        font-size: 2.08vmin;
}
.shogi-player .board_wrap .overlay_navi {
  cursor: pointer;
}
.shogi-player .board_wrap .overlay_navi.previous {
    cursor: w-resize;
}
.shogi-player .board_wrap .overlay_navi.next {
    cursor: e-resize;
}
.shogi-player .board_wrap .overlay_navi.board_turn_area {
    cursor: ns-resize;
}
.shogi-player .turned .overlay_navi.previous {
  cursor: e-resize;
}
.shogi-player .turned .overlay_navi.next {
  cursor: w-resize;
}
.shogi-player.debug .controller_block {
  background: rgba(191, 64, 64, 0.25);
}
.shogi-player.debug .board_wrap .overlay_navi.previous {
  background: rgba(166, 191, 64, 0.25);
}
.shogi-player.debug .board_wrap .overlay_navi.next {
  background: rgba(64, 191, 115, 0.25);
}
.shogi-player.debug .board_wrap .overlay_navi.board_turn_area {
  background: rgba(64, 115, 191, 0.25);
}
.shogi-player.debug .comment_area {
  background: rgba(166, 64, 191, 0.25);
}
.shogi-player.debug .piece_stand {
  background: rgba(191, 64, 64, 0.25);
}
.shogi-player.theme-simple .board {
  border-collapse: separate;
  border: 1px solid #999999;
  border-spacing: 1px;
  background: #999999;
}
.shogi-player.theme-simple .board td {
    font-size: 2.6vmin;
    color: #333333;
    width: 4vmin;
    height: 4vmin;
    background: white;
}
.shogi-player.theme-simple .board td.current {
      background: #e6e6e6;
}
.shogi-player.theme-simple .board td.origin_point {
      background: #f2f2f2;
}
.shogi-player.theme-simple .board td.current {
      color: #333333;
      font-weight: bold;
      -webkit-transition: background 500ms 0s ease-out;
      transition: background 500ms 0s ease-out;
}
.shogi-player.theme-simple .board td .span_cell {
      display: inline-block;
}
.shogi-player.theme-simple .board td .span_cell.location_black {
        -webkit-transform: translateY(5%);
                transform: translateY(5%);
}
.shogi-player.theme-simple .board td .span_cell.location_white {
        -webkit-transform: rotate(180deg) translateY(5%);
                transform: rotate(180deg) translateY(5%);
}
.shogi-player.theme-tree .board {
  border: 2px solid rgba(0, 0, 0, 0.4);
  position: relative;
}
.shogi-player.theme-tree .board:after {
    content: "";
    background-image: url(/static/ban_kaya_c.png);
    background-position: center;
    background-size: cover;
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}
.shogi-player.theme-tree .board td {
    color: transparent;
    width: 6vmin;
    height: 6vmin;
    border: 1px solid rgba(0, 0, 0, 0.4);
}
.shogi-player.theme-tree .board td.current {
      background: rgba(0, 0, 0, 0.05);
}
.shogi-player.theme-tree .board td.origin_point {
